@import "../../shared/styles/global.scss";

@media screen and (min-width: 320px) and (max-width: 768px) {
	.news {
		width: 100%;

		.container {
			width: 320px;
			padding-top: 20px;
		}

		.newItem {
			display: none;
		}

		.newCards {
			grid-template-columns: 1fr 1fr;
			gap: 16px;
		}
	}
}
